const WebFormList = ({ webFields, removeFromList }) => {
  const list = webFields.map(({ _id, fieldName, fieldInForm, fieldFormVisible, fieldOptions }) =>
    fieldInForm ?
         (
      <li key={_id}>
        {fieldName} {fieldFormVisible ? `-> visible in form` : `-> Not visible with ${fieldOptions.filter(option => option.preselected)}`} 
        <button id={fieldName} onClick={removeFromList}>
          X
        </button>
      </li>
    ) : null
  );

  return <ul>{list}</ul>;
};

export default WebFormList;
